	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>Initial Assessment</strong></p>\n<p>I will ask you a series of questions pertaining to your health history and\nwill assess your posture and identify weakness and will suggest ways improve\nyour current condition.</p>\n<p><strong>Swedish Massage</strong></p>\n<p>This is a light to moderate approach, therefore, the focus is on relaxation,\nimproving circulation and giving you a boost of energy. Swedish massage can\nalso aid after injury.</p>\n<p><strong>Neuromuscular Therapy</strong></p>\n<p>If you suffer from Chronic pain, nerve compression or postural issues, this\nform of massage manipulates soft tissue involving muscular and the nervous\nsystem.</p>\n<p><strong>Deep Tissue</strong></p>\n<p>This is more therapeutic and will focus on your stiff muscle pain and joints.\nThe approach is deeper and slower and will focus on locating those trigger\npoints and contracture muscle areas. Over time, those areas become stagnant\nand through the help of deep tissue massage, we are able to release and\nimprove blood flow.</p>\n<p><strong>Sports Massage</strong></p>\n<p>Some injuries may occur due to a sport or in some sort of training. This\napproach focuses on pre and post events, both with different results. Sports\nMassage can also promote flexibility and help to prevent injury.</p>\n<p><strong>Pregnancy Massage</strong></p>\n<p>Your body will go through many changes so depending on what stage you are in\n(pre or post), pregnancy massage can help with reducing pain, improving\ncirculation, reducing edema in the legs and feet.</p>","frontmatter":{"title":"Modalities","heroImage":"/img/modalities-lucy.jpg"}}},"pathContext":{"id":"/Users/jamie/Documents/workspace/lucytumolo-gatsby/src/pages/modalities/index.md absPath of file >>> MarkdownRemark"}}

